import { Link } from "react-router-dom"
import { Container, Row, Col, Navbar, Nav, NavDropdown, Form, FormControl, Button } from 'react-bootstrap';
import NAV_F from '../images/navneet-futurtech.png';
import LOGO from '../images/logo.svg';
const About = () => {
    return (<>
    <section className="full no-pad" id="about">
        <div className="header about-header-bg">
            <div className="container">
                <h1>About</h1>
            </div>
        </div>
        <div className="container">
            <div className="row ">
                <div className="col future-tech-col">
                    <div className="future-tech">
                        <div className="logo-img">
                            <img src={NAV_F} alt=""/>
                        </div>
                        <h3 className="bold">About Navneet Futuretech </h3>
                        <p>Navneet Education Limited (formerly known as Navneet Publication Limited), began its story in pre independent India! It was founded by the Gala Group in 1959, and has since then been a trusted and reliable educational content provider.</p>

                        <p>Our immensely popular products like Vikas workbooks, Gala practice books, and YouVa full-scapes, are favorites amongst all eager learners. At Navneet we take immense pride in providing quality work in whatever we do.</p>
                        <p>Over the decades, Navneet has emerged as the preferred brand for educational products among both teachers and students.</p>
                    </div>
                </div>
                <div className="col leapbridge-col">
                    <div className="leapbridge">
                        <div className="logo-img">
                            <img src={LOGO} alt=""/>
                        </div>
                        <h3 className="bold">About Leapbridge</h3>
                        <p> In 2009 the Leapbridge International Pre-Schools was conceived by Navneet Education Limited to disseminate knowledge and provide a strong early year Pre-Primary education. However, we soon realised, the power of digital and Leapbridge
                            is now an early year digital learning solution.</p>

                        <p>Our purpose has always been to impart knowledge and inspire students. We strive to deliver good educational content that engages, entertains, and helps develop essential skills in our children. At Leapbridge, content is curated
                            carefully and always considers the latest scientific research in early year development.</p>
                        <p>In our role as education providers and knowledge enablers, we understand that digital only solutions fall short of delivering a holistic learning solution and have since developed a physical kit that blends the essential physical
                            activities with digital lessons well.</p>
                        <p>
                            As teachers and educationists, we take great care to ensure that our products follow a structured learning process that is age appropriate. It allows children to truly enjoy and understand.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </section>
    </>)
}

export default About