import React from 'react';
import { Container, Row, Col, Navbar, Nav, NavDropdown, Form, FormControl, Button, Dropdown } from 'react-bootstrap';
import useAuth from '../hooks/useAuth';
import { useNavigate, Link } from "react-router-dom";
import { useContext } from "react";
import AuthContext from "../context/AuthProvider";
import LOGO from '../images/logo.png'

const Navigation = (props) => {
  const { setAuth } = useContext(AuthContext);
  const navigate = useNavigate();
  const { auth } = useAuth();
  const logout = async () => {
    // if used in more components, this should be in context 
    // axios to /logout endpoint 
    setAuth({});
    localStorage.removeItem("user");
    localStorage.removeItem("accessToken");
    navigate('/');
  }
  return <>
    <Navbar collapseOnSelect expand="lg" bg="light" variant="light">
      <Container>
        <Navbar.Brand href="/"><img className="header-logo" src={LOGO} alt="" /></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav" className='justify-content-end'>
          <Nav className="me-auto">
            {/* <Link className="nav-link" to="/kit">Kit</Link> */}
            {/* <Link className="nav-link" href="/developers">Developers</Link> */}
            {/* <Link className="nav-link" href="/dashboard">Dashboard</Link> */}
          </Nav>
          <Nav>
            < Link className="nav-link" to="/home">HOME</ Link>
            <Link className="nav-link" to="/product">PRODUCTS</Link>
            <Link className="nav-link" to="/careers">CAREERS</Link>
            <Link className="nav-link" to="/about">ABOUT</Link>
            {auth?.user
              ?
              <Dropdown>
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                  User
                </Dropdown.Toggle>
                <Dropdown.Menu>
                   <Dropdown.Item href="/profile">Profile</Dropdown.Item>
                  <Dropdown.Item><Button variant="link" type="button" onClick={logout}>SIGN OUT </Button></Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>

              : <>
                <Link className="nav-link" to="/signin">SIGN IN</Link></>
            }
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar><div className="color-stripe">
      <span className="yellow"></span>
      <span className="orange"></span>
      <span className="blue"></span>
      <span className="pink"></span>
      <span className="red"></span>
    </div>
  </>
    ;
}

export default Navigation; 