import { Button, Col, Container, Card, Form, InputGroup, FormControl, Row } from 'react-bootstrap';
import { useState, useEffect } from "react";
import axios from 'axios';
import _ from 'lodash'
function Developer() {
    const URL = "https://dev-api-leapbridge.demotrt.com/v1";
    // LOC STATE
    const [games, setGames] = useState(null);
    const setColor = (skillName) => {
        switch (skillName) {
            case "Language": return  "l-blue"
            case "Creative Skills": return  "l-brown"
            case "Know My World": return  "l-pink"
            case "Maths": return  "l-purple"
            case "Critical Skills": return  "l-yellow"
            case "Social Emotional": return  "l-orange"
            default:
                break;
        }
     }
    const getGamesCat = (games) => {
        const key = 'skillName';
        const arrayUniqueByKey = [...new Map(games.map(item => [item[key], item])).values()];
        return arrayUniqueByKey.map((cat) => <Card key={cat._id} className={setColor(cat.skillName)}><Card.Body> {cat.skillName} </Card.Body></Card>)
    }

    const getCategory = (catg) => {
        const johnArr = _.filter(catg, game => game.skillName === 'Language');
        console.log(johnArr)
    }

    const getGamesData = async (e) => {
        axios.get(
            URL + "/unit",
            {
                headers: { 'Content-Type': 'application/json' }
            })
            .then(function (response) {
                console.log("RETURN RESP", response.data.data);
                response.data.data && setGames(response.data.data);
            })
            .catch(function (error, response) {
                console.log("RETURN ERROR", error, response);
            });
    }

    useEffect(() => {
        //console.log("LODASH", _);
    }, [games]);

    return (
        <div className="">
            <h1>DEVELOPERS</h1>
            <Button variant="primary" type="button" onClick={() => getGamesData()}> Get Games Data </Button>

            <Container>
                <Row>
                    <Col sm={4}>{
                        games != null
                            ?
                            <>
                                <h3>  Categorys</h3>
                                {getGamesCat(games)}

                            </>
                            :
                            <p>No Games found</p>
                    }</Col>
                    <Col sm={8}>
                        <h3> Games</h3>
                        {
                            games != null
                                ?
                                <>
                                    {
                                        games.map((game) =>
                                            <Card key={game._id}>
                                                <Card.Body>
                                                    {game.contentCategory}
                                                    <br />
                                                    {game.assetName}
                                                    <br />
                                                    {game.assetCode}
                                                </Card.Body>
                                            </Card>)
                                    }
                                </>
                                :
                                <p>No Games found</p>
                        }</Col>
                </Row>
            </Container>

        </div>
    );
}

export default Developer;

