import { useState, useEffect } from "react";
import { Container, Modal, Alert, Row, Col, Navbar, Nav, NavDropdown, Form, FormControl, Button } from 'react-bootstrap';
import axios from "axios";
const URL = 'https://dev-api-leapbridge.demotrt.com/v1'
const Product = () => {
    const [page, setPage] = useState(1);
    const [commitHistory, setCommitHistory] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [children, setChildren] = useState();
    const [selectedChild, setSelectedChild] = useState();
    const [plans, setPlans] = useState();
    //MODAL STATE
    const [show, setShow] = useState(false);

    const handleClose = () => {
        // setShow(false);
    };
    const handleShow = () => {
        // setShow(true)
    };
    const getPlans = () => {
        let childAccessToken = localStorage.getItem('child');
        console.log(childAccessToken);
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${childAccessToken}`
        }
        const data = {
            "minAge": 3,
            "maxAge": 3
        }
        axios.post(`${URL}/plan`, data, {
            headers: headers
        })
            .then((response) => {
                console.log("-----------------------PLANS LIST", response);
                console.log("-----------------------PLANS LIST", response.data.data);
                setPlans(response.data.data)
            })
            .catch((error) => {
                console.log(error);
            })
    };


    useEffect(() => {
        let user = localStorage.getItem('user');
        let userJSON = JSON.parse(user);
        // console.log("USE EFFECT-----FROM PRODUCT PAGE ---------------------", userJSON.children);
        // setChildren(userJSON.children)
    }, [])

    useEffect(() => {
        let accessToken = localStorage.getItem('accessToken');
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${accessToken}`
        }
        const data = {
            "platform": "android",
            "identifier": "android"
        }
        axios.post(`${URL}/child/auth/6256902b2c0766001c6f9d73`, data, {
            headers: headers
        })
            .then((response) => {
                // localStorage.setItem('child', JSON.stringify(response.data.data.token));
                localStorage.setItem('child', response.data.data.token);
            })
            .catch((error) => {
                console.log(error);
            })
    }, [plans]);


    return (<>
        <Container>
            <div className="store-header">
                <h1 className="bold">The Leapbridge Store</h1>
                <Button variant="primary" onClick={getPlans}> Get Plans </Button>
            </div>
        </Container>
        <section className="container">
            <div className="age-groups">
                <div className="group two-plus">
                    <div>
                        <div className="year">
                            2+ Years
                        </div>
                        <div className="title">
                            Creators
                        </div>
                        <div className="subtitle">
                            Getting their early development right
                        </div>
                    </div>
                </div>
                <div className="group three-plus">
                    <div>
                        <div className="year">
                            3+ Years
                        </div>
                        <div className="title">
                            Explorers
                        </div>
                        <div className="subtitle">
                            Getting their early development right
                        </div>
                    </div>
                </div>
                <div className="group four-plus">
                    <div>
                        <div className="year">
                            4+ Years
                        </div>
                        <div className="title">
                            Explorers
                        </div>
                        <div className="subtitle">
                            Getting their early development right
                        </div>
                    </div>
                </div>
                <div className="group five-plus">
                    <div>
                        <div className="year">
                            5+ Years
                        </div>
                        <div className="title">
                            Inventors
                        </div>
                        <div className="subtitle">
                            Getting their early development right
                        </div>
                    </div>
                </div>
            </div>
        </section>

         
        <section className="container" id="">
            <div className="i-phone-grid">
                 {plans != null
                    ? plans.map((item, index) => (
                        <div key={item._id} className={"box box"+index+""}>
                            <div className="title">
                                {item.name}
                                <span>{item.duration}</span>
                            </div>
                            <h1 className="amount">
                                {item.amount}
                            </h1>
                            <p>{item.description}</p>
                            <button className="btn btn-primary btn-block">View</button>
                        </div>
                    ))
                    : <Alert variant="primary">
                        No Plans Found!
                    </Alert>} 
            </div>
        </section>
         


        <section className="container" id="">
            <div className="i-phone-grid">
                <div className="box box1">
                    <div className="title">
                        App only
                        <span>One Quarter</span>
                        <div className="price"><span>&#8377;</span> 799</div>
                    </div>
                    <button className="btn btn-primary">View</button>
                </div>
                <div className="box box2">
                    <div className="title">
                        App only <span>Annual</span>
                        <div className="price"><span>&#8377;</span> 1099</div>

                    </div>
                    <button className="btn btn-primary">View</button>
                </div>
                <div className="box box3">
                    <div className="title">
                        Kit + App <span>One Quarter</span>
                        <div className="price"><span>&#8377;</span> 2799</div>

                    </div>
                    <button className="btn btn-primary">View</button>
                </div>
            </div>
            <div className="i-phone-grid">
                <div className="box box6">
                    <div className="title">
                        Kit + App <span>Annual</span>
                    </div>
                    <button className="btn btn-primary">View</button>
                </div>
                <div className="box box5">
                    <div className="title">
                        App only <span>One Quarter</span>
                    </div>
                    <button className="btn btn-primary">View</button>
                </div>
                <div className="box box4">
                    <div className="title">
                        App only <span>One Quarter</span>
                    </div>
                    <button className="btn btn-primary">View</button>
                </div>
            </div>
        </section>

        <Modal show={show} onHide={handleClose} centered backdrop="static">
            <Modal.Header closeButton>
                <Modal.Title>Confirm Kid's Age</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    {/* 
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control
                            type="email"
                            placeholder="name@example.com"
                            autoFocus
                        />
                    </Form.Group>
                     */}
                    <Form.Select aria-label="Default select example">
                        {/* {children != null ? children.map((item, index) => (
                        <option key={index}>{index}</option>
                    )) : <>Text</>}  */}
                        <option value="1">1+</option>
                        <option value="2">2+</option>
                        <option value="3">3+</option>
                        <option value="4">4+</option>
                        <option value="5">5+</option>
                    </Form.Select>
                </Form>
            </Modal.Body>
            <Modal.Footer>
                {/* <Button variant="secondary" onClick={handleClose}>
            Close
          </Button> */}
                <Button variant="primary" onClick={handleClose}>
                    Save
                </Button>
            </Modal.Footer>
        </Modal>
    </>)
}

export default Product