import { Link } from "react-router-dom"
import { Container, Row, Col, Navbar, Nav, NavDropdown, Form, FormControl, Alert, Button } from 'react-bootstrap';
import JOBS from '../api/jobs.json'
const Careers = () => {
    return (<>
        <section className="full no-pad" id="career">
            <div className="header career-header-bg">
                <div className="container">
                    <h1>Careers</h1>
                </div>
            </div>
            <div className="container career">
                <div className="row ">
                    <div className="col">
                        <div className="sec-header">
                            <h1 className="bold">We're looking to improve how children are educated.</h1>
                            <h3>Excited about how technology can educate kids? We'd like to hear from you!</h3>
                        </div>
                        {JOBS != null
                            ? JOBS.map((item, index) => (
                                <div key={index} id="job-cards" className="job-cards typ-inline">
                                    <div className="job-card ">
                                        <div className="title"> <h3>{item.Title}</h3> </div>
                                        <div className="location"><small>{item.Location.formatted}</small>
                                            <small>Positions -   </small></div>
                                        <div className="cta">
                                            <Link
                                                to={{ pathname: `/job-details/${item.ID}` }}
                                                state={{ job: item }}
                                            >
                                                <button className="btn btn-primary">View</button>
                                            </Link>
                                            {/* <a className="btn btn-primary" href="./job.html?job=${value.Title}">View</a> */}
                                        </div>
                                    </div>
                                </div>
                            ))
                            : <Alert variant="primary">
                                No Jobs Found!
                            </Alert>}
                    </div>
                </div>
            </div>
        </section>

        <section className="full">
            <div className="container">
                <div className="row">
                    <div className="col">
                        <h1 className="text-center bold">Didn’t find the position you’re looking for? </h1>
                        <h1 className="text-center bold"> send us your CV</h1>
                        <div className="text-center">
                            <button className="btn btn-lg btn-primary">Apply Now</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    </>)
}

export default Careers