import { Link } from "react-router-dom"
import { Container, Row, Col, Card, Nav, NavDropdown, Form, FormControl, Button } from 'react-bootstrap';
const Checkout = () => {
    return (
        <Container>
            <Row>
                <Col sm={7}>
                    <Card>
                        <Card.Header>Contact Info</Card.Header>
                        <Card.Body>
                            {/* <Card.Title>Special title treatment</Card.Title> */}
                            <Form>
                                <Form.Group className="mb-3" controlId="">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control  type="email" placeholder="Enter email" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="">
                                    <Form.Label>Mobile</Form.Label>
                                    <Form.Control  type="number" placeholder="Mobile" />
                                </Form.Group>
                            </Form>
                            <Button variant="primary">NEXT</Button>
                        </Card.Body>
                    </Card>
                    <br />
                    <br />
                    <Card>
                        <Card.Header>Shipping Address</Card.Header>
                        <Card.Body>
                            <Form>
                                <Row>
                                    <Col sm={6}>
                                        <Form.Group className="mb-3" controlId="">
                                            <Form.Label>Email</Form.Label>
                                            <Form.Control  type="text" placeholder="First Name" />
                                        </Form.Group>
                                    </Col>
                                    <Col sm={6}>
                                        <Form.Group className="mb-3" controlId="">
                                            <Form.Label>Mobile</Form.Label>
                                            <Form.Control  type="text" placeholder="Last Name" />
                                        </Form.Group>
                                    </Col>
                                    <Form.Group className="mb-3" controlId="">
                                        <Form.Label>Delivery Address*</Form.Label>
                                        <Form.Control  type="text" placeholder="Delivery Address*" />
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="">
                                        <Form.Label>Apartment, suite, unit, etc.(optional)</Form.Label>
                                        <Form.Control  type="text" placeholder="Apartment, suite, unit, etc.(optional)" />
                                    </Form.Group> 
                                    <Col sm={6}>
                                        <Form.Group className="mb-3" controlId="">
                                            <Form.Label>City</Form.Label>
                                            <Form.Control  type="text" placeholder="City" />
                                        </Form.Group>
                                    </Col>
                                    <Col sm={6}>
                                        <Form.Group className="mb-3" controlId="">
                                            <Form.Label>State</Form.Label>
                                            <Form.Control  type="text" placeholder="State" />
                                        </Form.Group>
                                    </Col>
                                    <Col sm={6}>
                                        <Form.Group className="mb-3" controlId="">
                                            <Form.Label>Country</Form.Label>
                                            <Form.Control  type="text" placeholder="Country" />
                                        </Form.Group>
                                    </Col>
                                    <Col sm={6}>
                                        <Form.Group className="mb-3" controlId="">
                                            <Form.Label>Zip Code</Form.Label>
                                            <Form.Control  type="text" placeholder="Zip Code" />
                                        </Form.Group>
                                    </Col>
                                </Row>
                            </Form>
                            <Button variant="primary">NEXT</Button>
                        </Card.Body>
                    </Card>
                    <br />
                    <br />
                    <Card>
                        <Card.Header>Make Payment</Card.Header>
                        <Card.Body>
                            <Button variant="primary">Pay Online</Button>
                        </Card.Body>
                    </Card>
                </Col>
                <Col sm={5}><Card>
                    <Card.Body>This is some text within a card body.</Card.Body>
                </Card></Col>
            </Row>
        </Container>
    )
}

export default Checkout