import { Button, Col, Container, Form, InputGroup, FormControl, Row } from 'react-bootstrap';
import { useState, useEffect } from "react";
import axios from 'axios';

function Register() {
  const [token, setToken] = useState("");

  const [mobileNumber, setMobileNumber] = useState("");
  const [OTP, setOTP] = useState("");
  const [varifyOTP, setVerifyOTP] = useState(false);
  const [viewOtpForm, setViewOtpForm] = useState(false);

  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [message, setMessage] = useState("");
  const [gender, setGender] = useState("");
  const [dob, setDob] = useState("");
  const [ageGroup, setAgeGroup] = useState("");
  const [avatarForChild, setAvatarForChild] = useState("");
  const [avatarForYourSelf, setAvatarForYourSelf] = useState("");
  const [questionAnswers, setQuestionAnswers] = useState("");

  useEffect(() => {
    if (token) {
      console.log("TOKEN", token);
    }
    if (varifyOTP) {
      setViewOtpForm(false)
    }
  }, [token, varifyOTP])

  const URL = "https://dev-api-leapbridge.demotrt.com/v1";

  const DATA = {
    phone: mobileNumber,
    email: "mukeshcgs@gmail.com",
    name: "0010c",
    gender: "boy",
    dob: "12-12-2021",
    ageGroup: "2+",
    avatarForChild: "4542454756453",
    avatarForYourSelf: "456434345512",
    questionAnswers: [
      {
        question: 1,
        answer: 1
      },
      {
        question: 2,
        answer: 3
      },
      {
        question: 3,
        answer: 2
      },
      {
        question: 4,
        answer: 2
      },
      {
        question: 5,
        answer: 1
      },
      {
        question: 6,
        answer: 3
      }
    ],
    language: "english",
    notification: {
      email: true
    }
  }

  const sendOtpBtn = async (e) => {
    console.log("MOBILE NUMBER", mobileNumber);
    axios.post(
      URL + "/parent/sendotp",
      {
        phone: mobileNumber
      },
      {
        headers: { 'Content-Type': 'application/json' }
      })
      .then(function (response) {
        console.log("RETURN RESP", response);
        response.data.status == "OK" && setViewOtpForm(true)
      })
      .catch(function (error, response) {
        console.log("RETURN ERROR", error, response);
      });
  };

  const verifyOtpBtn = async (e) => {
    console.log("OTP", OTP);
    axios.post(
      URL + "/parent/confirmotp",
      {
        phone: "+91"+mobileNumber,
        otp: OTP
      },
      {
        headers: { 'Content-Type': 'application/json' }
      })
      .then(function (response) {
        console.log("RETURN RESP", response);
        console.log("RETURN RESP", response.data.data.parent);
        response.data.status == "OK" && setVerifyOTP(true)
        response.data.data.token && setToken({ token: response.data.data.token })
      })
      .catch(function (error, response) {
        console.log("RETURN ERROR", error, response);
      });
  };

  let handleSubmit = async (e) => {
    e.preventDefault();
    axios.post(URL + "/parent/register", DATA, {
      headers: { 'Content-Type': 'application/json' }
    })
      .then(function (response) {
        console.log("RETURN RESP", response.data);
        response.data.data.token && setToken({ token: response.data.data.token })
      })
      .catch(function (error) {
        console.log(error);
        console.log("RETURN ERROR", error);
      });
  };
  
  let getParentInfo =  (e) => {
    console.log("TOKEN on getting parent info", token);
     axios.get(URL + "/parent/me", {
      headers: { "Authorization": `Bearer ${token}` }
    })
    .then(function (response) {
      console.log("RETURN RESP", response.data);
    })
    .catch(function (error) {
      console.log("RETURN ERROR", error);
    });
  }

  return (
    <div className="App">
      <header className="App-header"> </header>
      <Container>
        <Row>
        <Col sm> 
          </Col>
          <Col sm>
            <div>
              <h3>Register</h3>
               <h1>{message}</h1>
              <Form onSubmit={handleSubmit}>
                <InputGroup className="mb-3">
                  <FormControl
                    type="phone"
                    placeholder="+91 987 654 3210"
                    aria-label="Phone"
                    aria-describedby="basic-addon2"
                    value={mobileNumber}
                    onChange={(e) => {
                      setMobileNumber(e.target.value);
                      setVerifyOTP(false);
                    }}
                  />{!varifyOTP
                    ? 
                    <InputGroup.Text id="basic-addon2" onClick={() => sendOtpBtn()}>Send OTP</InputGroup.Text>
                    :
                    <InputGroup.Text id="basic-addon2" >Verified</InputGroup.Text>
                  }
                </InputGroup>
                <InputGroup className="mb-3">
                  {viewOtpForm ? < >
                    <FormControl
                      type="number"
                      placeholder="Enter OTP"
                      value={OTP}
                      onChange={(e) => {
                        setOTP(e.target.value)
                      }}
                    />
                    <InputGroup.Text id="basic-addon2" onClick={() => verifyOtpBtn()}>Verify</InputGroup.Text>
                  </ >
                    : ""}
                </InputGroup>

                {/* EMAIL */}
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Enter email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Form.Group>
                {/* NAME */}
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter name"
                    value={email}
                    onChange={(e) => setName(e.target.value)}
                  />
                </Form.Group>
                {/* BOY/GIRL */}
                <Form.Group className="mb-3" controlId=" ">
                  {['radio'].map((type) => (
                    <div key={`inline-${type}`} className="mb-3">
                      <Form.Check
                        inline
                        label="Girl"
                        name="group1"
                        type={type}
                        id={`inline-${type}-1`}
                      />
                      <Form.Check
                        inline
                        label="Boy"
                        name="group1"
                        type={type}
                        id={`inline-${type}-2`}
                      />
                    </div>
                  ))}
                </Form.Group>
                {/* DOB */}
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Date of Birth</Form.Label>
                  <Form.Control type="date" placeholder="Date of birth" />
                </Form.Group>
                {/* AGE GROUP */}
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Age Group</Form.Label>
                  <Form.Control type="text" placeholder="Age Group" />
                </Form.Group>
                {/* CHILD AVATAR */}
                <Form.Group className="mb-3" controlId="">
                  <Form.Select aria-label="Default select example">
                    <option>Avatar For Child</option>
                    <option value="1">One</option>
                    <option value="2">Two</option>
                    <option value="3">Three</option>
                  </Form.Select>
                </Form.Group>
                {/* PARENT AVATAR */}

                <Form.Group className="mb-3" controlId="">
                  <Form.Select aria-label="Default select example">
                    <option>Avatar For Parent</option>
                    <option value="1">One</option>
                    <option value="2">Two</option>
                    <option value="3">Three</option>
                  </Form.Select>
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicCheckbox">
                  <Form.Check type="checkbox" label="notification" />
                </Form.Group>

                <Button variant="primary" type="submit"> Submit </Button>

              </Form>
            </div>
          </Col>
          <Col sm> 
          </Col>
        </Row>
      </Container>

    </div>
  );
}

export default Register;

 