import React from "react";
import ReactDOM from "react-dom";
import { useSearchParams } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
const WAIT_INTERVAL = 1000;
const LEADSQURE_URL = "https://api-in21.leadsquared.com/v2/LeadManagement.svc/Lead.Capture?accessKey=u$r0b1c9ee98f17fa63958064aaf15b9a0b&secretKey=8e426ea0ca1e0ddfcc46fc0458b5d8c1888ecfbd"

function LeadForm() {
    const [mobileValidate, setMobileValidate] = React.useState(false);
    const [mobileNumber, setMobileNumber] = React.useState("");
    const [OTP, setOTP] = React.useState(false);
    const [otpNumber, setOTPNumber] = React.useState("");
    //URL PARAM
    const [searchParams, setSearchParams] = useSearchParams();

    const [state, setState] = React.useState({
        username: "",
        KidsAge: "",
        email: "",
        phone: "",
        otp: ""
    });

    const { register, watch, handleSubmit, reset, trigger, formState: { errors, isValid, isValidating } } = useForm({
        mode: 'onChange',
        reValidateMode: 'onBlur'
    });
    console.log("00001", searchParams.get("utm_source"));

    React.useEffect(() => {
        console.log("mobileValidate", mobileValidate);
        mobileNumber !== "" && getOTP(mobileNumber);
    }, [mobileValidate, mobileNumber])

    const onSubmit = async (data) => {
        console.log("DATA", data);
        let url = "https://dev-api-leapbridge.demotrt.com/v1/Webuser";
        try {
            const rawResponse = await fetch(url, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            });

            const content = await rawResponse.json();
            console.log("ON SUBMIT RESPONCE", content);

            if (content.status === "OK") {
                reset({
                    username: "",
                    KidsAge: "",
                    email: "",
                    phone: "",
                    otp: ""
                })

                //LEADSQUARED
                ////////////////////////////////////////////////////////////////////LEADSQURE DATA POST
                const LEADSQURE_URL = "https://api-in21.leadsquared.com/v2/LeadManagement.svc/Lead.Capture?accessKey=u$r0b1c9ee98f17fa63958064aaf15b9a0b&secretKey=8e426ea0ca1e0ddfcc46fc0458b5d8c1888ecfbd"

                const LeadSquiredDATA = [{
                    "Attribute": "FirstName",
                    "Value": data.username
                },
                {
                    "Attribute": "mx_Age_Group",
                    "Value": data.KidsAge
                },
                {
                    "Attribute": "EmailAddress",
                    "Value": data.email
                },
                {
                    "Attribute": "Phone",
                    "Value": data.phone
                },
                {
                    "Attribute": "Source",
                    "Value": searchParams.get("utm_source")
                },
                {
                    "Attribute": "mx_Sub_Source",
                    "Value": searchParams.get("utm_medium")
                },
                {
                    "Attribute": "mx_Ad",
                    "Value": searchParams.get("utm_campaign")
                },
                {
                    "Attribute": "mx_Ad_Group",
                    "Value": searchParams.get("utm_id")
                }
                ]

                const rawLeadResp = await fetch(LEADSQURE_URL, {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(LeadSquiredDATA)
                });

                const LEAD_RESP = await rawLeadResp.json();

                console.log("LEAD_RESP", LEAD_RESP);
                ////////////////////////////////////////////////////////////////////LEADSQURE DATA POST
                console.log("FORM SENDING SUCCESS");
            } else {
                console.log("FORM SENDING ERROR")
            }

        } catch (error) {
            console.log("SEND OTP FAILD", error);
        }
    };



    async function getOTP(mobileNumber) {
        let url = "https://dev-api-leapbridge.demotrt.com/v1/parent/sendotp";
        try {
            if (mobileNumber !== "") {
                let MOBILE_DATA = { "phone": "+91" + mobileNumber }
                const rawResponse = await fetch(url, {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(MOBILE_DATA)
                });
                const resOTP = await rawResponse.json();
                console.log("SEND OTP", resOTP);
            } else {
                console.log("SEND OTP FAILD");
            }

        } catch (error) {
            console.log("SEND OTP FAILD", error);
        }
    }

    function handleChange(evt) {
        const value = evt.target.value;
        setState({
            ...state,
            [evt.target.name]: value
        });
    }


    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            {/* <label htmlFor="username">First Name</label> */}
            <input
                className="form-control"
                placeholder="First name"
                {...register("username", { validate: (value) => value !== "" })}
                onChange={handleChange}
            // value={state.username}
            />
            {errors.username && <small>Your name required</small>}
            {/* <label htmlFor="KidsAge">Age</label> */}
            <select
                name="KidsAge"
                className="form-control"
                onChange={handleChange}
                {...register("KidsAge", { validate: (value) => value !== "" })} >
                <option value="2">2+</option>
                <option value="3">3+</option>
                <option value="4">4+</option>
                <option value="5">5+</option>
            </select>
            {errors.KidsAge && <small>Your KidsAge required</small>}
            {/* <label htmlFor="email">Email</label> */}
            <input
                className="form-control"
                placeholder="bluebill1049@hotmail.com"
                type="email"
                {...register("email", { validate: (value) => value !== "" })}
                onChange={handleChange}
            // value={state.email}
            />
            {errors.email && <small>Your email required</small>}
            <br />
            {/* <label htmlFor="lastName">Mobile</label> */}
            <input
                type="text"
                className="form-control"
                placeholder="Mobile No."
                {...register("phone", {
                    // maxLength: 10,
                    validate: async (value) => {
                        if (value !== "" && value.length == 10) {
                            // console.log("Mobile True");
                            setMobileValidate(true);
                            setMobileNumber(value);
                            return true
                        } else {
                            // console.log("Mobile False");
                            setMobileValidate(false)
                            setMobileNumber("value");
                            return false
                        }
                    }
                })}
            />
            {errors.phone && <small>Your phone is not valid</small>}
            <input
                type="text"
                className="form-control"
                placeholder="OTP"
                {...register("otp", {
                    // maxLength: 10,
                    validate: async (value) => {
                        if (value !== "" && value.length == 4) {
                            // console.log("Mobile True");
                            setOTP(true);
                            setOTPNumber(value);
                            return true
                        } else {
                            // console.log("Mobile False");
                            setOTP(false)
                            setOTPNumber("value");
                            return false
                        }
                    }
                })}
            />
            {errors.otp && <small>Your otp is not valid</small>}
            <br />


            <div className="d-flex justify-content-center">
                <button type="submit" className="btn btn-primary" id="submitForm">Join Now</button>
            </div>
        </form>
    );
}

export default LeadForm