import React from 'react';
import { useState, useEffect } from "react";
import RootRoutes from './rootRouter';
import Navigation from './components/Navbar';
import Footer from './components/Footer';
// import RouterExample from './router';
import { BrowserRouter, HashRouter } from 'react-router-dom';

import { UserConsumer } from './context/userContext';
import { AuthConsumer } from './context/authContext';
import XPro, { MyPromise } from './utils/boot';
import { isLogin, getToken } from './utils';
import useAuth from './hooks/useAuth';


function App() {
  const [user, setUser] = useState({});
  const [signout, signin] = useState({});
  const { auth, setAuth } = useAuth();

  useEffect(() => {
    // const data =  JSON.stringify(localStorage.getItem('user'));
    let user = JSON.parse(localStorage.getItem('user'));
    const accessToken = localStorage.getItem('accessToken');
    if (accessToken !== null) {
      setAuth({ user, accessToken });
    }
      //user = JSON.parse(localStorage.getItem('user'));
    // let xx = JSON.parse(data)
    // console.log("USE EFFECT-----FROM App.js", user);
    // console.log("USE EFFECT-----FROM App.js", accessToken);
  }, []);

   return (
    <RootRoutes />
  );
}

if (isLogin) {
  XPro().then(() => App()).catch(error => console.error("----------------App Error", error));
} else {
  getToken();
}

export default App;

